.topic-card-wrapper {
  width: 202px;
  height: 140px;
  box-shadow: 0 0 3px 0 #d1d6d8;
  border-radius: 3px;
  transition: all 0.1s ease-in-out;
}

.topic-card-wrapper:hover {
  background-color: #22366f;
  color: white;
}

.topic-card-wrapper > a,
.topic-card-text-svg-wrapper {
  height: 100%;
  width: 100%;
}

.topic-card-wrapper > a {
  color: #22366f;
  text-decoration: none;
}

.topic-card-wrapper > a:hover {
  color: white;
}

.topic-card-text-svg-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
}
